@font-face {
  font-family: "futura";
  src: url("../fonts/futubk-webfont.woff");
  src: url("../fonts/futubk-webfont.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "futura";
  src: url("../fonts/futurabookbt-webfont.woff");
  src: url("../fonts/futurabookbt-webfont.woff2") format("woff2");
  font-style: normal;
  font-weight: 600;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* Global classes */

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 34px;
  min-height: 100px;
}

.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .nav .mobail-menu {
  display: none;
  width: 30px;
  height: 30px;
  border: none;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  position: relative;
  background-color: transparent;
}

.header .nav .mobail-menu:before {
  content: '';
  position: absolute;
  top: 50%;
  width: 100%;
  border-top: 2px solid black;
  transform: translate(0, -50%);
}

.header .nav .mobail-menu.closer {
  position: fixed;
  z-index: 10;
  outline: none;
  top: 50px;
  right: 20px;
  border: none;
  transition: 1s;
}

.header .nav .mobail-menu.closer:before,
.header .nav .mobail-menu.closer:after {
  content: '';
  display: block;
  min-width: 100%;
  position: absolute;
  border: 1px solid #ffff;
  outline: none;
  transform-origin: 50% 50%;
  transition: 0.5s;
}

.header .nav .mobail-menu.closer:before {
  transform: rotate(-45deg);
}

.header .nav .mobail-menu.closer:after {
  transform: rotate(45deg);
}

.header .nav-logo {
  display: relative;
  z-index: 10;
  font-size: 18px;
  text-decoration: none;
  color: black;
  width: 37px;
  height: 37px;
}

.header .nav-logo__img {
  min-width: 37px;
  min-height: 37px;
}

.header .nav-menu {
  display: flex;
  display: relative;
  z-index: 10;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  align-self: center;
}

.header .nav-menu.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 7;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.header .nav-menu__li {
  margin-right: 30px;
  font-size: 12px;
}

.header .nav-menu__li:nth-child(5) {
  margin-right: 90px;
}

.header .nav-menu__li_a {
  text-decoration: none;
  font-family: Futura Bk BT;
  font-size: 12px;
  line-height: 14px;
  color: #000000;
}

.header .nav-menu__li_youtube {
  align-self: center;
  text-decoration: none;
  color: black;
  font-size: 14px;
  line-height: 21px;
}

.header .nav-menu__li_youtube:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/Vector.svg);
  width: 21px;
  height: 16px;
  margin-right: 10px;
}

